import React from 'react';
import { Form, Text, TextArea } from 'react-form';

import 'whatwg-fetch';

// Components
import Spinner from 'components/ui/spinner';

// Styles
import './styles.scss';

class Contact extends React.Component {
  state = {
    submitting: false,
    submitted: false
  }

  onSubmit = (values) => {
    this.setState({ submitting: true });
    const {
      firstName,
      lastName,
      email,
      tel,
      observations
    } = values;

    fetch(`https://script.google.com/macros/s/AKfycbxSOt5Wzx8traptkU0elMVQSFiJC-rQcgwuezw9cN-Lpo6zSog/exec?firstName=${firstName}&lastName=${lastName}&email=${email}&tel=${tel}&observations=${observations}`, { method: 'GET' })
      .then(() => {
        this.setState({
          submitting: false,
          submitted: true
        });
        console.info('Success');
      })
      .catch((error) => {
        console.error('request failed', error);
      });
  }

  render() {
    const { submitting, submitted } = this.state;

    return (
      <div className="c-contact">
        {submitting && <Spinner />}

        <div className="wrapper">
          <div className="row center-xs">
            <div className="col-xs-12 col-sm-6">
              <h2>Contacto</h2>

              <Form onSubmit={this.onSubmit}>
                {formApi => (
                  <form onSubmit={formApi.submitForm} id="contact-form">
                    <div className="c-field">
                      <label htmlFor="firstName">Nombre *</label>
                      <Text field="firstName" id="firstName" required />
                    </div>

                    <div className="c-field">
                      <label htmlFor="lastName">Apellidos *</label>
                      <Text field="lastName" id="lastName" required />
                    </div>

                    <div className="c-field">
                      <label htmlFor="email">Email *</label>
                      <Text field="email" id="email" type="email" required />
                    </div>

                    <div className="c-field">
                      <label htmlFor="tel">Tel√©fono *</label>
                      <Text field="tel" id="tel" type="tel" pattern="^[0-9-+s()]*$" required />
                    </div>

                    <div className="c-field">
                      <label htmlFor="observations">Observaciones</label>
                      <TextArea field="observations" id="observations" rows="8" />
                    </div>

                    {!submitted &&
                      <button
                        type="submit"
                        className={classnames({
                          'c-form-btn': true,
                          '-success': submitted
                        })}
                      >
                        Enviar
                      </button>
                    }

                    {submitted &&
                      <button disabled type="submit" className="c-form-btn -success">
                        Gracias por contactar con nosotros
                      </button>
                    }
                  </form>
                )}
              </Form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Contact;



